import React, { ReactNode, isValidElement } from "react";
import * as ContextMenu from "@radix-ui/react-context-menu";
import {
  FiChevronRight,
} from "react-icons/fi";
import { styled, alpha } from "@mui/material";
import { CONTENT_STYLES, ITEM_STYLES, ITEM_THEMED_STYLES, TRIGGER_THEMED_STYLES, DropdownData, RightSlot, LeftSlot } from "./NestedDropdown";

export interface NestedContextMenuProps<T> {
  data: T;
  onClick?: (data: T) => void;
  inner: DropdownData<T>[];
  children?: ReactNode;
}

const ContextMenuTrigger = styled(ContextMenu.Trigger)(
  ITEM_STYLES,
  TRIGGER_THEMED_STYLES,
);
const ContextMenuSubTrigger = styled(ContextMenu.SubTrigger)(
  ITEM_STYLES,
  TRIGGER_THEMED_STYLES,
);
const ContextMenuContent = styled(ContextMenu.Content)(
  CONTENT_STYLES,
  ({ theme }) => ({
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.background.paper, 0.7),
  }),
);

const ContextMenuSubContent = styled(ContextMenu.SubContent)(
  CONTENT_STYLES,
  ({ theme }) => ({
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.background.paper, 0.7),
  }),
);

const ContextMenuItem = styled(ContextMenu.Item)(
  ITEM_STYLES,
  ITEM_THEMED_STYLES,
);

const ContextMenuSeparator = styled(ContextMenu.Separator)(
  {
    height: 1,
    margin: 5,
  },
  ({ theme }) => ({
    backgroundColor: theme.palette.primary.main,
  }),
);

const ContextMenuLabel = styled(ContextMenu.Label)(
  {
    paddingLeft: 25,
    fontSize: 12,
    lineHeight: 2.5,
  },
  ({ theme }) => ({
    color: alpha(theme.palette.primary.main, 0.8),
  }),
);

const ContextMenuArrow = styled(ContextMenu.Arrow)({}, ({ theme }) => ({
  fill: alpha(theme.palette.background.paper, 0.7),
}));

function InnerContext<T>({
  data,
  inner,
}: {
  data: T;
  inner: DropdownData<T>;
}) {
  if (inner.type === "DIVIDER") {
    return <ContextMenuSeparator />;
  } else if (inner.type === "HEADER") {
    return (
      <ContextMenuLabel>
        {typeof inner.label == "function"
          ? inner.label(data)
          : inner.label
          ? inner.label
          : "Header"}
      </ContextMenuLabel>
    );
  } else if (inner.inner) {
    return (
      <ContextMenu.Sub>
        <ContextMenuSubTrigger>
          {typeof inner.label == "function"
            ? inner.label(data)
            : inner.label
            ? inner.label
            : "Menu Item"}
          <RightSlot>
            {isValidElement(inner.right) ? (
              (inner.right as ReactNode)
            ) : typeof inner.right == "function" ? (
              inner.right(data)
            ) : (
              <FiChevronRight />
            )}
          </RightSlot>
        </ContextMenuSubTrigger>
        <ContextMenu.Portal>
          <ContextMenuSubContent sideOffset={2} alignOffset={-5}>
            {inner.inner.map((innerData: DropdownData<T>, i: number) => (
              <InnerContext key={i} data={data} inner={innerData} />
            ))}
          </ContextMenuSubContent>
        </ContextMenu.Portal>
      </ContextMenu.Sub>
    );
  } else {
    return (
      <ContextMenuItem
        onSelect={(e: Event) => {
          if (typeof inner.onClick === "function") {
            inner.onClick(data);
            if (inner.preventCloseOnClick) {
              e.preventDefault();
            }
          }
        }}
      >
        {inner.left && (
          <LeftSlot>
            {isValidElement(inner.left)
              ? (inner.left as ReactNode)
              : typeof inner.left == "function"
              ? inner.left(data)
              : inner.left}
          </LeftSlot>
        )}
        {typeof inner.label == "function"
          ? inner.label(data)
          : inner.label
          ? inner.label
          : "Menu Item"}
        {inner.right && (
          <RightSlot>
            {isValidElement(inner.right)
              ? (inner.right as ReactNode)
              : typeof inner.right == "function"
              ? inner.right(data)
              : inner.right}
          </RightSlot>
        )}
      </ContextMenuItem>
    );
  }
}

export function NestedContextMenu<T>(props: NestedContextMenuProps<T>): ReactNode {
  return (
    <ContextMenu.Root>
      <ContextMenuTrigger asChild>
        {props.children}
      </ContextMenuTrigger>

      <ContextMenu.Portal>
        <ContextMenuContent>
          {props.inner.map((innerData: DropdownData<T>, i: number) => (
            <InnerContext key={i} data={props.data} inner={innerData} />
          ))}
          <ContextMenuArrow />
        </ContextMenuContent>
      </ContextMenu.Portal>
    </ContextMenu.Root>
  );
}
